.production-process {
  margin-right: 30px;
  margin-left: 30px;
  margin-bottom: 58px;
  padding-top: 25px;
  width: 260px;
  background-color: $color-white;
  border: 1px solid $color-grey-light;

  &:hover,
  &:focus {
    box-shadow: 0 6px 10px rgba($color: $color-black, $alpha: 0.1);
  }

  &:active {
    box-shadow: 0 3px 5px rgba($color: $color-black, $alpha: 0.1);
  }
}

.production-process__title {
  position: relative;
  margin: 0;
  margin-bottom: 23px;
  margin-right: 30px;
  margin-left: 90px;

  @include font-bold;
  @include font(17px, 24px, $color-black);

  &::before {
    position: absolute;
    content: "";
    width: 32px;
    height: 32px;
    top: 5px;
    left: -60px;
    background-image: url("../img/icon-video.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.production-process__video {
  padding: 0;
  margin-bottom: 25px;
}

.production-process__description {
  margin-bottom: 25px;
  margin-left: 30px;
  margin-right: 40px;

  @include font-regular;
  @include font(13px, 24px, $color-grey-dark);
}

.production-process__order {
  margin-left: 35px;
  margin-bottom: 35px;
  display: block;
  width: 188px;
}

@media (min-width: $width-tablet) {
  .production-process {
    position: relative;
    margin: 495px 50px 65px 50px;
    padding: 50px 45px 43px 40px;
    width: 668px;
    box-sizing: border-box;
  }

  .production-process__title {
    font-size: 20px;
    margin: 0;
    margin-left: 65px;
    margin-bottom: 30px;

    &::before {
      top: -5px;
      left: -65px;
    }
  }

  .production-process__video {
    margin: 0;
    position: absolute;
    top: -445px;
    left: 0;
  }

  .production-process__description {
    margin: 0;
    margin-bottom: 35px;
    font-size: 15px;
    line-height: 30px;
  }

  .production-process__order {
    margin: 0;
    width: 582px;
  }
}

@media (min-width: $width-desktop) {
  .production-process {
    margin: 0;
    margin-bottom: 105px;
    margin-left: 50px;
    padding: 0;
    padding-left: 760px;
    padding-right: 58px;
    padding-top: 95px;
    width: 1050px;
    min-height: 455px;
  }

  .production-process__title {
    margin: 0;
    margin-bottom: 35px;

    &::before {
      top: -55px;
      left: 0;
    }
  }

  .production-process__video {
    top: 0;
  }

  .production-process__description {
    margin-bottom: 45px;
  }

  .production-process__order {
    width: 226px;
    margin-bottom: 30px;
  }
}
